import './styles.css';
import { Component } from 'preact';
import {sortableContainer, 
    sortableElement, 
    sortableHandle} from 'react-sortable-hoc';
import {arrayMoveImmutable} from 'array-move';
import { csvParse } from 'd3-dsv';
import * as glc from 'GLCvis'


const DragHandle = sortableHandle(() => <div className='icon'></div>);

const SortableItem = sortableElement(({value}) => (
    <div className='Showcase__style__stylizedItem Showcase__style__item'>
      <DragHandle />
      {value}
    </div>
  ));
  
const SortableContainer = sortableContainer(({children}) => {
    return <div>{children}</div>;
});

class SidePanel extends Component {

    constructor(props){
        super(props)
        console.log(this.props)
        this.state = {
            data: [],
            dimensions: [],
            target: null,
            angles: [],
            colorize: true,
            dynamic: false,
            dualview:false,
            opacity: 1
        }
        this.chart = null
        this.chart2 = null
    }

    changeSelect = e => {
        this.setState({value_layout: e.target.value})
    }

    toggleColorize = e => {
        let checked = !this.state.colorize;
        this.setState({colorize: checked});
        this.chart.colorize(checked)
        if (this.props.dualview)
            this.chart2.colorize(checked)
    };

    toggleDynamic = e => {
        let checked = !this.state.dynamic;
        this.setState({dynamic: checked});
    };

    onSortEnd = ({oldIndex, newIndex}) => {
        this.setState(({dimensions, angles}) => ({
            dimensions: arrayMoveImmutable(dimensions, oldIndex, newIndex),
            angles: arrayMoveImmutable(angles, oldIndex, newIndex)
        }))

    };   

    onLoad = () => {   

        let parseFile = () => {
            let doesColumnExist = false;
            let data = csvParse(reader.result, function(d){
                doesColumnExist = d.hasOwnProperty("someColumn");
                return d;   
            });
            this.setState({
                data:data,
                dimensions: data.columns.slice(0, -1),
                target: data.columns.slice(-1)
            })
        }

        let reader = new FileReader();
        let file = document.querySelector('input[type=file]').files[0]; 
        reader.addEventListener("load", parseFile, false);
        if (file) {
            reader.readAsText(file);
        }      
    }

    onAngleChange = e => {
        let t  = this.state.angles
        t[e.target.name] = e.target.value
        this.setState({
            angles:t
        })
    }

    hdlOpacity = e => {
        this.setState({opacity:e.target.value})
        this.chart.setOpacity(e.target.value)
        if (this.props.dualview)
            this.chart2.setOpacity(e.target.value)

    }

    hdlInfo = e => {
        alert("The dataset input requieres a CSV file where the last column is the target value (categorical data type).")
    }

    redrawing = () => {

        let element = document.getElementById("plot")
        if (element.firstChild)
            element.removeChild(element.firstChild)

        this.chart = glc.newChart({
            data: this.state.data,
            width: 1450,
            height: 400, 
            selector: "#plot", 
            type: this.props.layout, 
            dimensions: this.state.dimensions,
            target: this.state.target,
            dynamic: this.state.dynamic
        })

        if (this.props.dualview){

            element = document.getElementById("plot2")
            if (element.firstChild)
                element.removeChild(element.firstChild)

            this.chart2 = glc.newChart({
                data: this.state.data,
                width: 1450,
                height: 400, 
                selector: "#plot2", 
                type: this.props.layout2, 
                dimensions: this.state.dimensions,
                target: this.state.target,
                dynamic: this.state.dynamic
            })
 
        }

        if (['seq-generic', 'nseq-generic'].includes(this.props.layout)){
            if (this.state.angles.length === 0){
                let angles= Array.from({length: this.state.dimensions.length}, () => Math.floor(Math.random() * 360));
                this.setState({angles:angles})
                this.chart.setOrientations(angles)
            }else
                this.chart.setOrientations(this.state.angles)
        }
  
        this.chart.draw()
        this.chart.colorize(this.state.colorize)
        this.chart.setOpacity(this.state.opacity)
        
        if (this.props.dualview){
            this.chart2.draw()
            this.chart2.colorize(this.state.colorize)
            this.chart2.setOpacity(this.state.opacity)
            this.chart.setDualView(this.chart2)
        }
    }

    render(){

        var dimensions  = this.state.dimensions

        return (
            <div className='sidepanel'>

                <div className="subpanel">

                Dataset <input type='file' onChange={this.onLoad}/> <a class="bi bi-info-circle-fill" onClick={this.hdlInfo}></a>

                <br/>
                <br/>

                <strong>Examples</strong>
                <li><a href="/assets/data/iris.csv"> Iris Dataset</a></li>
                <li><a href="/assets/data/cars.csv"> Cars Dataset</a> </li>
                <li><a href="/assets/data/wine.csv"> Wine Dataset</a></li>

                <br/>
                <br/>

                <SortableContainer onSortEnd={this.onSortEnd} useDragHandle>
                    {dimensions.map((value, index) => (
                        <SortableItem key={`item-${value}`} index={index} value={value} />
                    ))}
                </SortableContainer>

                <br/>
                
                {['seq-generic', 'nseq-generic'].includes(this.props.layout) &&
                    <ul>
                        Angles
                        {this.state.angles.map((value, index) => (
                            <li> {dimensions[index]} <input type="number" value={Math.round(value)}
                            min="0" max="360" name={index} onChange={this.onAngleChange}/> </li>
                        ))}
                    </ul>
                }  
                
                <label>
                    <span> Opacity </span>
                    <input type="range" min="0" max="1" step="0.01" onChange={this.hdlOpacity}/>
                </label>

                <br/>
                <br/>

                <label>
                    <span> Colorize </span>
                    <input type="checkbox" id='colorize' checked={this.state.colorize} 
                        onClick={this.toggleColorize}/> 
                </label>

                <br/>
                <br/>

                <label>
                    <span> Dynamic </span>
                    <input type="checkbox" id='dynamic' checked={this.state.dynamic} 
                        onClick={this.toggleDynamic}/> 
                </label>

                <br/>
                <br/>

                <label>
                    <span> Dual View </span>
                    <input type="checkbox" id='dualview' checked={this.props.dualview} 
                        onClick={this.props.hdldual}/> 
                </label>

                <br/>
                <br/>

                </div>

                {this.state.data.length
                    ? <button onClick={this.redrawing} className="btn">Draw</button>
                    : <button onClick={this.redrawing} className="btn" disabled>Draw</button>
                }
            </div>
          );
    }

}
export default SidePanel;