import './style.css';
import SidePanel from "./SidePanel/SIdePanel";
import { Component } from 'preact';

export default class App extends Component {

	constructor(){
        super()
        this.state = {
            data: [],
            value_layout_np: 'parallel',
            value_layout_p: 'cpc',
            value_layout: 'parallel',
            layouts: [],                     
            layouts_np: [
                ['Parallel', 'parallel'],
                ['Bush', 'bush'], 
                ['Radial', 'radial'],
                ['n-Gon', 'polygon'],
                ['Circular', 'circular'],
                ['InLine', 'inline'],
                ['Non-Sequential Generic', 'nseq-generic'], 
                ['Sequential Generic', 'seq-generic'],
            ],
            layouts_p: [
                ['Collocated Paired', 'cpc'],
                ['Shifted Paired', 'spc'],
                ['Anchored Paired', 'apc'],
                ['Paired Radial Coordinates', 'prc'],
                ['Ellipse Paired Coordinates', 'epc'],
                ['Paired Crown Coordinates', 'pwc']
            ],
            dual: false
    	}
    	this.full_layouts = this.state.layouts_np.concat(this.state.layouts_p)
    	this.setState({layouts: this.full_layouts})
	}

    changeSelect = e => {
    	if (e.target.id == "view")
        	this.setState({value_layout: e.target.value})
        if (e.target.id == "view2")
        	this.setState({value_layout_p: e.target.value})
    }

    toggleDual = e => {
        let checked = !this.state.dual;
        if (checked){
        	this.setState({layouts:this.state.layouts_np})
        }else{
        	this.setState({layouts:this.full_layouts})
        }
        this.setState({dual: checked});
    };

	render(){

		return (
			<div>
				<SidePanel layout={this.state.value_layout} layout2={this.state.value_layout_p} dualview={this.state.dual} hdldual={this.toggleDual}/>

				<div className="content">
					<select id="view" className="selector" onChange={this.changeSelect}>
	                    { this.state.layouts.map( (layout) => (
	                        <option value={layout[1]}>{layout[0]}</option>
	                    )) }
	                </select>
					<div id='plot'></div>
				</div>

                {this.state.dual &&
			        <div className="content">
						<select id="view2" className="selector2" onChange={this.changeSelect}>
		                    { this.state.layouts_p.slice(0, -1).map( (layout) => (
		                        <option value={layout[1]}>{layout[0]}</option>
		                    )) }
		                </select>
						<div id='plot2'></div>
					</div>
			    }

			</div>
		);
	}
}
